body {
  background-image: url("b.cd93ef5d.jpg");
  background-size: 20%;
  overflow: hidden;
}

.contacts__box {
  width: 80%;
  height: 60vh;
  -webkit-backdrop-filter: blur(2px);
  backdrop-filter: blur(2px);
  border-radius: 30px;
  margin: 15vh auto 0;
  overflow: hidden;
}

@media screen and (min-width: 767.5px) {
  .contacts__box {
    display: flex;
  }
}

.contacts__box > ul {
  width: 50%;
  display: flex;
}

.m__t {
  background-color: #f0f8ff97;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.m__t p {
  color: #34495e;
  font-weight: bold;
}

.m__t li {
  width: 280px;
  height: 40px;
  cursor: pointer;
  background-color: #ffffffbc;
  border-radius: 20px;
  justify-content: center;
  align-items: center;
  margin: 20px 0;
  transition: all .5s;
  display: flex;
}

.m__t li:hover {
  background-color: #e67e22;
}

.messengers li:hover svg, .m__t li:hover p, .m__t li:hover svg {
  fill: #fff;
  color: #fff;
}

.m__t a {
  width: 100%;
}

.contacts__box svg {
  width: 30px;
  height: 30px;
  fill: #34495e;
  margin: 0 15px;
}

.messengers {
  background-color: #e67d223e;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.messengers li {
  width: 100%;
  width: 100px;
  height: 100px;
  border: 3px solid #34495e;
  border-radius: 20px;
  justify-content: center;
  align-items: center;
  margin: 20px;
  transition: all .5s;
  display: flex;
}

.messengers li:hover {
  background-color: #e67e22;
  border: 3px solid #fff;
}

.messengers svg {
  width: 50px;
  height: 50px;
  margin: 20px;
}

